<template>
  <div class="calendar-view">
    <div class="calendar">
      <vue-cal hide-view-selector style="height: 500px" locale="zh-cn" active-view="month"
        :disable-views="['years', 'year', 'week', 'day']" :time="false" events-on-month-view="true"
        :events="classCalendar.concat(restCalendar)" @cell-click="handleCellClick($event)">
      </vue-cal>
    </div>
    <p></p>
    <el-card class="box-card">
      周期开始日期
      <el-date-picker v-model="inputDateBegin" @change="handleInputChange()" type="date" placeholder="选择开始日期" />
      <p></p>
      周期结束日期
      <el-date-picker v-model="inputDateEnd" @change="handleInputChange()" type="date" placeholder="选择结束日期" />
    </el-card>
    <el-dialog :visible.sync="dialogVisible" title="修改日期类型" @close="resetDialog" width="80%">
      <span>{{ dialogText }}</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="handleDateTypeChange">修改</el-button>
        <el-button @click="dialogVisible = false">取消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import dayjs from 'dayjs';
import VueCal from 'vue-cal';
import 'vue-cal/dist/vuecal.css';
import isSameOrBefore from 'dayjs/plugin/isSameOrBefore'

export default {
  name: 'CalendarView',
  components: {
    VueCal
  },
  data() {
    return {
      dialogVisible: false,
      dialogText: '',
      selectedDate: null,
      dateOperationType: 0,
      dateBegin: dayjs('2024-09-29'), // 周期开始日期
      dateEnd: dayjs('2024-12-31'), // 周期结束日期
      daysOfCycle: 6, // 每个周期的天数
      datesIncluded: [dayjs('2024-09-29'), dayjs('2024-10-12')],
      datesExcluded: [dayjs('2024-10-01'), dayjs('2024-10-02'), dayjs('2024-10-03'), dayjs('2024-10-04'), dayjs('2024-10-07')],
      datesClass: [],
      datesRest: [],
      classCalendar: [],
      restCalendar: [],
      inputDateBegin: null,
      inputDateEnd: null,
      classInfoList: [
        {
          classId: 1,
          classDay: 1
        },
        {
          classId: 2,
          classDay: 2
        },
      ]
    }
  },
  methods: {
    isWeekday(dateCurrent) {
      return (dateCurrent.day() == 0 || dateCurrent.day() == 6) ? false : true;
    },
    isIncluded(dateCurrent) {
      return this.datesIncluded.some(date => date.isSame(dateCurrent, 'day'))
    },
    isExcluded(dateCurrent) {
      return this.datesExcluded.some(date => date.isSame(dateCurrent, 'day'))
    },
    updateClass(classInfo, index) {
      const classDay = classInfo.classDay; // 任务在每个周期的第几天
      const classId = classInfo.classId;
      let dateCurrent = dayjs(this.dateBegin); // 当前日期
      let dayCurrent = 1; // 当前日期是当前周期的第几天
      let cycleCurrent = 1; // 当前周期
      let datesClassTemp = [];
      while (dateCurrent.isSameOrBefore(this.dateEnd, 'date')) {
        // （工作日 且 不在排除列表）或（休息日 且 在包含列表）
        if (
          (this.isWeekday(dateCurrent) && !this.isExcluded(dateCurrent)) ||
          (!this.isWeekday(dateCurrent) && this.isIncluded(dateCurrent))
        ) {
          if (dayCurrent == classDay) {
            datesClassTemp.push(dateCurrent.format('YYYY-MM-DD'));
          }
          if (dayCurrent == this.daysOfCycle) {
            dayCurrent = 1;
            cycleCurrent++;
          }
          else {
            dayCurrent += 1;
          }
        }
        dateCurrent = dateCurrent.add(1, 'day');
      }
      this.classCalendar = this.classCalendar.concat(datesClassTemp.map(date => ({
        start: date,
        end: date,
        title: classId.toString() + '',
        class: 'class-' + index.toString()
      })));
      this.datesClass = this.datesClass.concat(datesClassTemp);
    },
    updateDataAll() {
      this.datesClass = [];
      this.classCalendar = [];
      this.classInfoList.forEach((classInfo, index) => {
        this.updateClass(classInfo, index);
      })
      this.updateRest();
      this.saveCurrentData();
    },
    updateRest() {
      this.datesRest = [];
      let dateCurrent = dayjs(this.dateBegin); // 当前日期

      while (dateCurrent.isSameOrBefore(this.dateEnd, 'date')) {
        // （在排除列表）或（休息日 且 不在在包含列表）
        if (
          (this.isExcluded(dateCurrent)) ||
          (!this.isWeekday(dateCurrent) && !this.isIncluded(dateCurrent))
        ) {
          this.datesRest.push(dateCurrent.format('YYYY-MM-DD'));
        }
        dateCurrent = dateCurrent.add(1, 'day');
      }
      this.restCalendar = this.datesRest.map(date => ({
        start: date,
        end: date,
        title: '休息',
        class: 'rest'
      }));
    },
    handleInputChange() {
      this.dateBegin = dayjs(this.inputDateBegin);
      this.dateEnd = dayjs(this.inputDateEnd);
      this.updateDataAll();
    },
    resetDialog() {
      this.selectedDate = null; // 重置选中的日期
      this.dateOperationType = 0; // 重置操作类型
    },
    handleDateTypeChange() {
      switch (this.dateOperationType) {
        case 1: // 工作日 在排除列表 删除日期
          this.datesExcluded = this.datesExcluded.filter(date => !date.isSame(this.selectedDate, 'day'));
          break;
        case 2: // 工作日 正常 添加日期
          this.datesExcluded.push(this.selectedDate);
          break;
        case 3: // 休息日 在包含列表 删除日期
          this.datesIncluded = this.datesIncluded.filter(date => !date.isSame(this.selectedDate, 'day'));
          break;
        case 4: // 休息日 正常 添加日期
          this.datesIncluded.push(this.selectedDate);
          break;
        default:
          console.log('Date Operation Type Error')
          break;
      }
      this.updateDataAll();
      this.dialogVisible = false; // 关闭对话框
    },
    handleCellClick(event) {
      const clickedDate = dayjs(event)
      this.dialogText = '当前日期是 ' + clickedDate.format('YYYY-MM-DD');
      if (this.isWeekday(clickedDate)) { //工作日
        if (this.isExcluded(clickedDate)) { // 在排除列表
          this.dialogText += '，是工作日但不上课。若当天上课请点击[修改]。';
          this.dateOperationType = 1;
        }
        else {
          this.dialogText += '，是工作日。若当天不上课请点击[修改]';
          this.dateOperationType = 2;
        }
      }
      else { // 休息日
        if (this.isIncluded(clickedDate)) { // 在包含列表
          this.dialogText += '，是休息日但上课。若当天不上课请点击[修改]';
          this.dateOperationType = 3;
        }
        else {
          this.dialogText += '，是休息日。若当天上课请点击[修改]。';
          this.dateOperationType = 4;
        }
      }
      this.selectedDate = clickedDate; // 获取单击的日期
      this.dialogVisible = true; // 显示对话框
    },
    loadFromLocalStorage() {
      const storedData = localStorage.getItem('calendarAPPData');

      if (storedData) {
        console.log('Load from local storage.')
        const data = JSON.parse(storedData);
        this.dateBegin = dayjs(data.dateBegin);
        this.dateEnd = dayjs(data.dateEnd);
        this.datesIncluded = data.datesIncluded.map(date => dayjs(date));
        this.datesExcluded = data.datesExcluded.map(date => dayjs(date));
      } else {
        this.saveCurrentData();
      }
    },
    saveCurrentData() {
      const defaultData = {
        dateBegin: this.dateBegin.format('YYYY-MM-DD'),
        dateEnd: this.dateEnd.format('YYYY-MM-DD'),
        datesIncluded: this.datesIncluded.map(date => date.format('YYYY-MM-DD')),
        datesExcluded: this.datesExcluded.map(date => date.format('YYYY-MM-DD')),
      };

      localStorage.setItem('calendarAPPData', JSON.stringify(defaultData));
      console.log('Save current data.');
    },
  },
  mounted() {
    this.loadFromLocalStorage();
    this.inputDateBegin = this.dateBegin.toDate();
    this.inputDateEnd = this.dateEnd.toDate();
    dayjs.extend(isSameOrBefore);
    this.updateDataAll();
  }
}
</script>

<style>
.vuecal__event.class-0 {
  background-color: DarkBlue;
  border: 1px solid DarkBlue;
  color: #FFFFFF;
}

.vuecal__event.class-1 {
  background-color: DarkRed;
  border: 1px solid DarkRed;
  color: #FFFFFF;
}

.vuecal__event.rest {
  background-color: DarkGreen;
  border: 1px solid DarkGreen;
  color: #FFFFFF;
}

.el-row {
  margin-top: 20px;
  margin-bottom: 20px;
}
</style>